/* Global Styles */
@import url('https://fonts.googleapis.com/css2?family=Figtree:wght@300;400;600;700&display=swap');

:root {
  /* Colors */
  --dark-bg: #121212; /* Background like this site */
  --grid-bg: #1e1e1e; /* Slightly lighter dark for the grid */
  --grid-line: #444444; /* Subtle gray for grid lines */
  --timeline-past: rgba(0, 0, 255, 0.2); /* Transparent blue for past timeline */
  --timeline-future: rgba(128, 128, 128, 0.2); /* Transparent gray for future timeline */
  --text-color: #ffffff; /* White text */
  --button-bg: #2a2a2a; /* Modern rounded button background */
  --button-hover: #3c3c3c; /* Slightly brighter gray on hover */
  --present-marker: #ff5f5f; /* Bright red for present time marker */
  --border: #444444; /* Border around the screen */
  --shadow-color: rgba(0, 188, 212, 0.5); /* Glow effect */

  /* Font */
  --font-family: 'Figtree', sans-serif;

  /* General Spacing */
  --border-radius: 12px;
}

body {
  margin: 0;
  font-family: var(--font-family);
  background-color: var(--dark-bg);
  color: var(--text-color);
  overflow: hidden; /* No scrollbars */
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Top Menu Bar */
.navbar {
  background-color: var(--grid-bg);
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 4px var(--shadow-color);
}

.navbar-item {
  color: var(--text-color);
  font-weight: bold;
  text-decoration: none;
  margin: 0 10px;
}

.navbar-item:hover {
  color: var(--timeline-future);
}

.app-title {
  font-size: 1.5rem;
  font-weight: bold;
}

/* Hero Section */
.hero {
  background-color: var(--grid-bg);
  color: var(--text-color);
  text-align: center;
  padding: 50px 20px;
}

.hero .title {
  font-size: 3rem;
  font-weight: bold;
}

.hero .subtitle {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.hero .button {
  background-color: var(--button-bg);
  color: var(--text-color);
  font-weight: bold;
  padding: 10px 20px;
  border-radius: var(--border-radius);
  transition: background-color 0.3s ease;
  border: none;
}

.hero .button:hover {
  background-color: var(--button-hover);
}

/* Timeline Wrapper */
.timeline-wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: var(--dark-bg); /* Dark background */
  overflow: hidden;
  cursor: grab;
}

.timeline-wrapper:active {
  cursor: grabbing;
}

/* Grid styles */
.grid-cell {
  position: absolute;
  background-color: var(--grid-bg); /* Darker background for the grid */
  box-sizing: border-box;
  border: 1px solid var(--grid-line); /* Subtle gray border for grid cells */
}

/* Timeline styles */
.present-marker {
  position: absolute;
  width: 4px; /* Slightly wider for better visibility */
  height: 100%;
  background-color: var(--present-marker);
  box-shadow: 0 0 15px var(--present-marker); /* Enhanced glow */
  border-radius: 2px;
}

/* Smoother Dragging Transition */
.timeline-wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: var(--dark-bg);
  overflow: hidden;
  cursor: grab;
}

.timeline-wrapper:active {
  cursor: grabbing;
}

/* Floating Add Event Button */
.add-event-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: var(--button-bg);
  color: var(--text-color);
  padding: 15px;
  font-size: 18px;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 4px 6px var(--shadow-color);
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
  z-index: 4;
}

.add-event-button:hover {
  background-color: var(--button-hover);
  box-shadow: 0 6px 10px var(--shadow-color);
}

/* Responsive */
@media (max-width: 768px) {
  .hero .title {
    font-size: 2rem;
  }
  .hero .subtitle {
    font-size: 1.2rem;
  }
}
